export type Slashlink = {
  petname: string,
  slug: string
}

/** Create a slashlink-shaped object */
export const Slashlink = ({ petname, slug }: {
  petname: string,
  slug: string
}): Slashlink => ({
  petname,
  slug
})

/**
 * Get a URL for this slashlink
 * URL has a custom `noosphere://` protocol prefix
 */
export const toUrl = ({ petname, slug } : {
  petname: string,
  slug: string
}): string => `noosphere://@${petname}/${slug}`