import {Signal} from 'spellcaster/spellcaster.js'
import {cid, h} from 'spellcaster/hyperscript.js'
import {BylineSm} from './byline.js'
import * as Slashlink from './slashlink.js'
import {Excerpt, ExcerptBlockModel} from './excerpt.js'

export type TranscludeModel = {
  id: string,
  slashlink: Slashlink.Slashlink,
  blocks: Array<ExcerptBlockModel>
}

export const TranscludeModel = ({
  id=cid(),
  slashlink,
  blocks
}: Partial <TranscludeModel>): TranscludeModel => ({
  id,
  slashlink,
  blocks
})

export const Transclude = (state: Signal<TranscludeModel>): HTMLElement => {
  return h(
    'aside',
    {className: 'transclude anim-fade-in'},
    [
      h(
        'a',
        () => ({
          href: Slashlink.toUrl(state().slashlink),
          className: 'transclude-link vstack gap-sm',
        }),
        [
          BylineSm(() => state().slashlink),
          h(
            'div',
            {className: 'transclude-content'},
            [
              Excerpt(() => state().blocks)
            ]
          )
        ]
      )
    ]
  )
}