import {h, text} from 'spellcaster/hyperscript.js'
import {Signal, sample} from 'spellcaster/spellcaster.js'
import {Slashlink} from './slashlink.js'

/**
 * Create a small byline element
 * Takes up only one line.
 */
export const BylineSm = (state: Signal<Slashlink>): HTMLElement => {
  return h(
    'header',
    {className: 'byline'},
    [
      h(
        'span',
        { className: 'byline-petname accent-text semibold' },
        text(() => `@${state().petname}`)
      ),
      h(
        'span',
        { className: 'byline-slug secondary-text' },
        text(() => `/${state().slug}`)
      )
    ]
  )
}