import {EditorView, NodeView} from 'prosemirror-view'
import {h} from 'spellcaster/hyperscript.js'

export const boldNodeView = (
  node: Node,
  view: EditorView,
  getPos: () => number|undefined
): NodeView => {
  const contentDOM = h(
    'span',
    {
      contentEditable: true
    },
    [node.textContent]
  )

  const dom = h(
    'strong',
    {
      className: 'bold',
    },
    [contentDOM]
  )

  return {dom, contentDOM}
}