import {inputRules, textblockTypeInputRule} from "prosemirror-inputrules"
import {schema} from '../schema.js'

export const quoteblockInputRule = textblockTypeInputRule(
  /^>\s$/,
  schema.nodes.quoteblock
)

export const listblockInputRule = textblockTypeInputRule(
  /^-\s$/,
  schema.nodes.listblock
)

export const headingblockInputRule = textblockTypeInputRule(
  /^#\s$/,
  schema.nodes.headingblock
)

export const blockTypeInputRulePlugin = () => inputRules({
  rules: [
    quoteblockInputRule,
    listblockInputRule,
    headingblockInputRule
  ]
})