import {Schema, DOMParser} from "prosemirror-model"

export const schema = new Schema({
  nodes: {
    text: {
      group: "inline"
    },
    plainblock: {
      group: "block",
      content: "text*",
      toDOM() { return ["x-block", 0] },
      parseDOM: [{tag: "x-block"}]
    },
    quoteblock: {
      group: "block",
      content: "text*",
      toDOM() { return ["x-quoteblock", 0] },
      parseDOM: [{tag: "x-quoteblock"}]
    },
    listblock: {
      group: "block",
      content: "text*",
      toDOM() { return ["x-listblock", 0] },
      parseDOM: [{tag: "x-listblock"}]
    },
    headingblock: {
      group: "block",
      content: "text*",
      toDOM() { return ["x-headingblock", 0] },
      parseDOM: [{tag: "x-headingblock"}]
    },
    doc: {
      content: "(plainblock|quoteblock|listblock|headingblock)+"
    }
  },
  marks: {
    b: {
      toDOM() { return ["b", 0] }
    },
    i: {
      toDOM() { return ["i", 0] }
    },
    code: {
      toDOM() { return ["code", 0] }
    },
    slashlink: {
      attrs: {
        href: {}
      },
      toDOM: (node) => [
        "a",
        {
          class: 'slashlink',
          href: node.attrs.href
        },
        0
      ],
    },
    wikilink: {
      attrs: {
        href: {}
      },
      toDOM: (node) => [
        "a",
        {
          class: 'wikilink',
          href: node.attrs.href
        },
        0
      ],
    },
    bracketlink: {
      attrs: {
        href: {}
      },
      toDOM: (node) => [
        "a",
        {
          class: 'bracketlink',
          href: node.attrs.href
        },
        0
      ],
    },
    barelink: {
      attrs: {
        href: {}
      },
      toDOM: (node) => [
        "a",
        {
          class: 'barelink',
          href: node.attrs.href
        },
        0
      ],
    }
  }
})