import {signal} from 'spellcaster/spellcaster.js'
import {h, repeat} from 'spellcaster/hyperscript.js'
import {Transclude, TranscludeModel} from '../../common/transclude.js'
import {EditorView, NodeView} from 'prosemirror-view'
import {DummyTranscludeService} from '../../services/transclude-service.js'
import {generateSlashlink} from '../../services/dummy-content.js'

const transcludeService = DummyTranscludeService()

/**
 * BlockNodeView defines the DOM for an individual block.
 * A ProseMirror NodeView is a bit like a stand-alone widget for a single
 * block-level node. We can use NodeViews to define complex DOM that might be
 * more complex than a decoration.
 * 
 * Note: these only have to conform to the NodeView interface. They don't
 * strictly have to be a class.
 */
export const blockNodeViewWithTag = (
  tag: string,
  node: Node,
  view: EditorView,
  getPos: () => number|undefined
): NodeView => {
  const [transcludes, setTranscludes] = signal<Map<string, TranscludeModel>>(
    new Map()
  )

  transcludeService.fetchBatch([
    generateSlashlink()
  ]).then((transcludes) => {
    setTranscludes(transcludes)
  })

  const content = h(
    'div',
    {
      className: 'block-content',
      contentEditable: true
    }
  )

  const footer = h(
    'div',
    {
      className: 'block-footer vstack gap-sm',
      contentEditable: 'false'
    },
    repeat(transcludes, Transclude)
  )

  const block = h(
    tag,
    {
      className: 'block'
    },
    [
      content,
      footer
    ]
  )

  const ignoreMutation = (mutation: MutationRecord): boolean => {
    switch (mutation.type) {
    case 'childList':
      return mutation.target === footer
    case 'attributes':
      return mutation.target === footer
    default:
      return false
    }
  }

  return {
    // The overall DOM element
    dom: block,
    // The element the editor should manage for the block content
    contentDOM: content,
    ignoreMutation
  }
}

export const blockNodeView = (tag: string) => (
  node: Node,
  view: EditorView,
  getPos: () => number|undefined
) => blockNodeViewWithTag(tag, node, view, getPos)

export const plainNodeView = blockNodeView('x-block')
export const quoteNodeView = blockNodeView('x-quoteblock')
export const listNodeView = blockNodeView('x-listblock')
export const headingNodeView = blockNodeView('x-headingblock')