import {h, text, cid, repeat, indexById} from 'spellcaster/hyperscript.js'
import {Signal, computed} from 'spellcaster/spellcaster.js'

export type ExcerptBlockModel = {
  id: string,
  body: string
}

export const ExcerptBlockModel = ({
  id=cid(),
  body=''
}: Partial<ExcerptBlockModel>): ExcerptBlockModel => ({
  id,
  body
})

export const ExcerptBlock = (
  block: Signal<ExcerptBlockModel>
): HTMLElement => {
  return h(
    'p',
    {className: 'excerpt-block'},
    text(() => block().body)
  )
}

export const Excerpt = (
  blocks: Signal<Array<ExcerptBlockModel>>
) => {
  return h(
    'div',
    {className: 'excerpt vstack gap-xsm'},
    repeat(
      computed(() => indexById(blocks())),
      ExcerptBlock
    )
  )
}