import {Node} from 'prosemirror-model'
import {NodeWithPos} from './node-with-pos.js'
import {NodeChangeStep} from './node-change-step.js'

const recurseChangedDescendants = (
  old: Node,
  cur: Node,
  offset: number,
  f: (node: Node, pos: number) => void
): void => {
  const oldSize = old.childCount
  const curSize = cur.childCount
  outer: for (let i = 0, j = 0; i < curSize; i++) {
    const child = cur.child(i)
    for (let scan = j, e = Math.min(oldSize, i + 3); scan < e; scan++) {
      if (old.child(scan) == child) {
        j = scan + 1
        offset += child.nodeSize
        continue outer
      }
    }
    f(child, offset)
    if (j < oldSize && old.child(j).sameMarkup(child)) {
      recurseChangedDescendants(old.child(j), child, offset + 1, f)
    } else {
      child.nodesBetween(0, child.content.size, f, offset + 1)
    }
    offset += child.nodeSize
  }
}

/**
 * Helper for iterating through the nodes in a document that changed
 * compared to the given previous document. Useful for avoiding
 * duplicate work on each transaction.
 * 
 * Adapted from <https://github.com/ProseMirror/prosemirror-tables/blob/master/src/fixtables.ts#L17C1-L47C2>
 * MIT License <https://github.com/ProseMirror/prosemirror-tables?tab=MIT-1-ov-file#readme>
 */
export const changedDescendants = (
  old: Node,
  cur: Node,
  f: (node: Node, pos: number) => void
) => recurseChangedDescendants(old, cur, 0, f)

/**
 * Reduce over changed descendants
 */
export const reduceChangedDescendants = <T>(
  {old, cur}: NodeChangeStep,
  step: (result: T, node: NodeWithPos) => T,
  initial: T
): T => {
  let result = initial
  changedDescendants(old, cur, (node, pos) => {
    result = step(result, {node, pos})
  })
  return result
}