import {createEditor, fromJSON} from './editor/editor.js'
import {styles} from './styles.js'

// Defines a Web Component for the editor
export class EditorElement extends HTMLElement {
  #editor

  constructor() {
    super()
    this.attachShadow({mode: 'open'})

    const shadow = this.shadowRoot
    shadow.adoptedStyleSheets = styles

    const editorEl = document.createElement('div')
    editorEl.id = 'editor'
    editorEl.className = 'editor theme'
    shadow.append(editorEl)

    const contentEl = document.createElement('slot')
    contentEl.id = 'content'
    shadow.append(contentEl)

    this.#editor = createEditor(editorEl, contentEl)
  }

  /** Set the editor's state with a JSON-serializable object */
  set state(jsonState) {
    const state = fromJSON(jsonState)
    this.#editor.updateState(state)
  }

  /**
   * Get editor's current state as a JSON-serialization-compatible
   * object.
   */
  get state() {
    return this.#editor.state.toJSON()
  }
}

customElements.define('x-editor', EditorElement)