import {EditorState, Transaction} from "prosemirror-state"
import {Node} from "prosemirror-model"

/** Is transaction `docChanged` property set to true? */
const isDocChanged = (transaction: Transaction) => transaction.docChanged

/**
 * Did state change in any meaningful way?
 * Checks transactions and doc equality.
 * Useful for exiting early when responding to changes in appendTransaction.
 */
export const isStateChanged = (
  transactions: Array<Transaction>,
  oldState: EditorState,
  newState: EditorState
) => transactions.some(isDocChanged) && !oldState.doc.eq(newState.doc)

/**
 * Get the `to` position of a node.
 * This will be the position immediately after the node closing tag.
 * See https://prosemirror.net/docs/guide/#doc.indexing.
 */
export const getNodeToPos = (node: Node, pos: number) =>
  pos + node.nodeSize

/**
 * Get the start position of the inside of a node.
 * 
 * Node start and end tags each add one to the position count, so the start
 * position of the inside of the node is `position + 1`.
 * See https://prosemirror.net/docs/guide/#doc.indexing
 */
export const getNodeInsideFromPos = (pos: number) => pos + 1

/**
 * Get the position of the end of the inside of a node.
 * Node start and end tags each add one to the position count, so the returned
 * position will be `position + nodeSize - 1`.
 * See https://prosemirror.net/docs/guide/#doc.indexing
 */
export const getNodeInsideToPos = (node: Node, pos: number) =>
  pos + node.nodeSize - 1