import {EditorState, Plugin} from "prosemirror-state"
import {EditorView} from "prosemirror-view"
import {DOMParser} from "prosemirror-model"
import {keymap} from "prosemirror-keymap"
import {history} from "prosemirror-history"
import {baseKeymap} from "prosemirror-commands"
import {dropCursor} from "prosemirror-dropcursor"
import {gapCursor} from "prosemirror-gapcursor"
import {focusedBlockDecoration} from './plugins/focused-block.js'
import {
  headingNodeView,
  listNodeView,
  plainNodeView,
  quoteNodeView
} from './node-views/block-node-view.js'
import {boldNodeView} from './node-views/bold-node-view.js'
import {blockTypeInputRulePlugin} from './plugins/block-type-input-rule.js'
import {inlineSubtext} from './plugins/inline-subtext.js'
import {schema} from "./schema.js"

/** Create an editor state from a DOM node */
export const parseState = contentEl =>
  DOMParser.fromSchema(schema).parse(contentEl)

/** Create an editor state given a JSON serialization */
export const fromJSON = serializedState => EditorState.fromJSON(
  {
    schema,
    plugins: plugins()
  },
  serializedState
)

const editorClassPlugin = () => new Plugin({
  props: {
    attributes: {class: "editor-main"}
  }
})

const plugins = () => [
  keymap(baseKeymap),
  dropCursor(),
  gapCursor(),
  history(),
  focusedBlockDecoration(),
  editorClassPlugin(),
  blockTypeInputRulePlugin(),
  inlineSubtext()
]

export const createEditor = (editorEl, contentEl) => new EditorView(editorEl, {
  state: EditorState.create({
    doc: parseState(contentEl),
    plugins: plugins()
  }),
  nodeViews: {
    plainblock: plainNodeView,
    quoteblock: quoteNodeView,
    listblock: listNodeView,
    headingblock: headingNodeView,
    bold: boldNodeView,
  }
})
