/** Transform a CSS string into a constructed stylesheet */
export const stylesheet = (css: string) => {
  const stylesheet = new CSSStyleSheet()
  stylesheet.replaceSync(css)
  return stylesheet
}

/** Load a CSS file as a constructed stylesheet */
export const loadStylesheet = async (url: URL) => {
  const response = await fetch(url)
  const css = await response.text()
  const stylesheet = new CSSStyleSheet()
  await stylesheet.replace(css)
  return stylesheet
}

export const resetStyles = stylesheet(`
* {
  margin: 0;
  padding: 0;
}
`)

