/**
 * Theme defines CSS variables that can be used in other sheets
 * Assign this utility class to the outermost wrapper element
 */
export const themeStyles = stylesheet(`
.theme {
  --unit: 4px;
  --padding: calc(var(--unit) * 4);
  --padding-sm: calc(var(--unit) * 2);
  --radius: var(--unit);
  --radius: calc(var(--unit) * 2);
  --radius-sm: var(--unit);
  --radius-lg: calc(var(--unit) * 4);
  --font-family: system-ui, sans-serif;
  --font-size: 17px;
  --line-height: 22px;
  --font: var(--font-size)/var(--line-height) var(--font-family);

  --color-text: black;
  --color-secondary-text: #8A8A8E;
  --color-bg: white;
  --color-secondary-bg: color-mix(in srgb, var(--color-bg), transparent 50%);
  --color-accent: #842A9C;
  --color-link: var(--color-accent);
  --color-link-underline: color-mix(in srgb, var(--color-link), transparent 70%);
  --color-brand-grey: #DFDBEB;
  --color-bg-code: hsl(0deg 0% 0% / 0.07);

  color-scheme: light dark;
  display: block;
  color: var(--color-text);
  font: var(--font);
}

@media (prefers-color-scheme: dark) {
  .theme {
    --color-text: white;
    --color-secondary-text: #F3F3F8;
    --color-bg: black;
    --color-accent: #C570DB;
  }
}

::selection {
  background-color: color-mix(in srgb, var(--color-accent), transparent 80%);
}
`)

export const customStyles = stylesheet(`
.ProseMirror {
  font: var(--font);
}

.editor {
  color: black;
  background-clip: padding-box;
}

.type-body {
  font: var(--font);
}

.semibold {
  font-weight: 500;
}

.accent-text {
  color: var(--color-accent);
}

.secondary-text {
  color: var(--color-secondary-text);
}

.vstack {
  display: flex;
  flex-direction: column;
}

.gap {
  gap: var(--padding);
}

.gap-sm {
  gap: var(--padding-sm);
}

.gap-xsm {
  gap: var(--unit);
}

.transclude {
  --padding-v: var(--padding);
  --padding-h: var(--padding);
  background: var(--color-secondary-bg);
  border-radius: var(--radius-lg);
  overflow: hidden;
  padding: var(--padding-v) var(--padding-h);
}

.transclude-link {
  display: flex;
  flex-direction: column;
  color: inherit;
  text-decoration: inherit;
  -webkit-tap-highlight-color: transparent;
}

@keyframes fade-in {
  0% { opacity: 0; }
  100% { opacity: 1; }
}

.anim-fade-in {
  animation: fade-in 500ms ease-out;
}

.block {
  --indent: calc(var(--unit) * 6);
  --padding-v: calc(var(--unit) * 2);
  --padding-h: var(--padding);
  display: block;
  background-color: transparent;
  border-radius: 8px;
  transition: background-color 200ms ease-out;
  padding: var(--padding-v) var(--padding-h);
}

.block-content a {
  color: var(--color-link);
  text-decoration-color: var(--color-link-underline);
  text-decoration-thickness: 1px;
  text-underline-offset: 0.25em;
}

.block-content code {
  background-color: var(--color-bg-code);
  border-radius: var(--radius);
  padding: 2px;
  font-family: monospace;
}

.block-footer {
  user-select: none;
  --padding-v: calc(var(--unit) * 2);
  padding-top: var(--padding-v);
}

.block-footer:empty {
  display: none;
}

x-quoteblock.block .block-content {
  --quote-width: 3px;
  border-left: var(--quote-width) solid var(--color-accent);
  padding-left: calc(var(--indent) - var(--quote-width));
}

x-listblock.block .block-content {
  --bullet-height: var(--line-height);
  padding-left: var(--indent);
  position: relative;
}

x-listblock.block .block-content::before {
  content: "•";
  font-size: 24px;
  font-weight: bold;
  position: absolute;
  left: 0;
  color: var(--color-accent);
  line-height: var(--bullet-height);
  height: var(--bullet-height);
  width: var(--indent);
  text-align: left;
}

x-headingblock .block-content {
  font-weight: bold;
}
`)

/**
 * A frozen array of constructed stylesheets that comprise our
 * default styles
 */
export const styles = Object.freeze([
  resetStyles,
  proseMirrorStyles,
  themeStyles,
  customStyles
])