import {
  delegate,
  ParserState,
  isExhausted,
  isAtBeginning,
  consume,
  start,
  cut,
  peek,
  offsetFromMut,
  offsetToMut,
  Range,
  consumeMatch,
  consumeUntilMatch,
  consumeUntilMatchOrEnd,
  isTrailingPunctuation,
  lastCharInRange,
  firstCharInRange,
} from './parser.js'

// Re-export range type
export type {Range} from './parser.js'

export const TokenType = {
  bold: 'bold',
  italic: 'italic',
  code: 'code',
  wikilink: 'wikilink',
  bracketlink: 'bracketlink',
  barelink: 'barelink',
  slashlink: 'slashlink'
}

export type Token = {
  type: string,
  range: Range
}

export const parseInlineSubtext = (string: string) => {
  const tokens: Token[] = []
  consumeInlineSubtext(ParserState({string}), tokens)
  return tokens
}

const consumeInlineSubtext = (state: ParserState, tokens: Token[]) => {
  while (!isExhausted(state)) {
    start(state)
    if (isAtBeginning(state) && consumeMatch(state, 'http://')) {
      delegate(consumeBareLink, state, tokens)
    } else if (isAtBeginning(state) && consumeMatch(state, 'https://')) {
      delegate(consumeBareLink, state, tokens)
    } else if (isAtBeginning(state) && consumeMatch(state, '@')) {
      delegate(consumeSlashlink, state, tokens)
    } else if (isAtBeginning(state) && consumeMatch(state, '/')) {
      delegate(consumeSlashlink, state, tokens)
    } else if (consumeMatch(state, ' http://')) {
      delegate(consumeBareLink, state, tokens)
    } else if (consumeMatch(state, ' https://')) {
      delegate(consumeBareLink, state, tokens)
    } else if (consumeMatch(state, ' @')) {
      delegate(consumeSlashlink, state, tokens)
    } else if (consumeMatch(state, ' /')) {
      delegate(consumeSlashlink, state, tokens)
    } else if (consumeMatch(state, '*')) {
      delegate(consumeBold, state, tokens)
    } else if (consumeMatch(state, '_')) {
      delegate(consumeItalic, state, tokens)
    } else if (consumeMatch(state, '`')) {
      delegate(consumeCode, state, tokens)
    } else if (consumeMatch(state, '[[')) {
      delegate(consumeWikiLink, state, tokens)
    } else if (consumeMatch(state, '<')) {
      delegate(consumeBracketLink, state, tokens)
    } else {
      consume(state)
    }
  }
}

const consumeBold = (state: ParserState, tokens: Token[]) => {
  if (consumeUntilMatch(state, '*')) {
    tokens.push({type: TokenType.bold, range: cut(state)})
    return true
  }
  return false
}

const consumeItalic = (state: ParserState, tokens: Token[]) => {
  if (consumeUntilMatch(state, '_')) {
    tokens.push({type: TokenType.italic, range: cut(state)})
    return true
  }
  return false
}

const consumeCode = (state: ParserState, tokens: Token[]) => {
  if (consumeUntilMatch(state, '`')) {
    tokens.push({type: TokenType.code, range: cut(state)})
    return true
  }
  return false
}

const consumeWikiLink = (state: ParserState, tokens: Token[]) => {
  if (consumeUntilMatch(state, ']]')) {
    tokens.push({type: TokenType.wikilink, range: cut(state)})
    return true
  }
  return false
}

const consumeBracketLink = (state: ParserState, tokens: Token[]) => {
  if (consumeUntilMatch(state, '>')) {
    tokens.push({type: TokenType.bracketlink, range: cut(state)})
    return true
  }
  return false
}

/**
 * Clean up a range that is dilimited by a word boundary.
 * Mutates the range.
 * Returns the range.
 */
export const cleanWordBoundaryDelimitedRangeMut = (
  string: string,
  range: Range
) => {
  // Clean up
  if (firstCharInRange(string, range) === ' ') {
    offsetFromMut(range, 1)
  }
  if (lastCharInRange(string, range) === ' ') {
    offsetToMut(range, -1, string.length)
  }
  if (isTrailingPunctuation(lastCharInRange(string, range))) {
    offsetToMut(range, -1, string.length)
  }
  return range
}

const consumeBareLink = (state: ParserState, tokens: Token[]) => {
  if (consumeAddressBody(state)) {
    const {string} = state
    const range = cut(state)
    cleanWordBoundaryDelimitedRangeMut(string, range)
    tokens.push({
      type: TokenType.barelink,
      range
    })
    return true
  }
  return false
}

const consumeSlashlink = (state: ParserState, tokens: Token[]) => {
  if (consumeAddressBody(state)) {
    const {string} = state
    const range = cut(state)
    cleanWordBoundaryDelimitedRangeMut(string, range)
    tokens.push({
      type: TokenType.slashlink,
      range
    })
    return true
  }
  return false
}

/** Consume until end of address body or end of input */
const consumeAddressBody = (state: ParserState) => {
  while (!isExhausted(state)) {
    const char0 = peek(state)
    const char1 = peek(state, 1)
    if (char0 === ' ') {
      return true      
    } else if (isTrailingPunctuation(char0) && char1 === ' ') {
      return true
    }
    consume(state)
  }
  return true
}