import {EditorState, Plugin, PluginKey, Transaction} from "prosemirror-state"
import {
  isStateChanged,
  getNodeToPos,
  getNodeInsideFromPos,
  getNodeInsideToPos
} from "../lib/helpers.js"
import {schema} from "../schema.js"
import {reduceChangedDescendants} from "../lib/changed-descendants.js"
import {reduce} from "../../common/generator.js"
import {
  parseInlineSubtext,
  Token,
  Range,
  TokenType
} from "../../common/subtext/subtext.js"
import {NodeWithPos} from "../lib/node-with-pos.js"
import {offsetRangeMut} from "../../common/subtext/parser.js"

const boldMark = schema.marks.b

const tokenTypeToMark = (type: string) => {
  switch (type) {
    case TokenType.bold:
      return schema.marks.b
    case TokenType.italic:
      return schema.marks.i
    case TokenType.code:
      return schema.marks.code
    case TokenType.wikilink:
      return schema.marks.wikilink
    case TokenType.bracketlink:
      return schema.marks.bracketlink
    case TokenType.barelink:
      return schema.marks.barelink
    case TokenType.slashlink:
      return schema.marks.slashlink
    default:
      return null
  }
}

export const inlineSubtext = () => new Plugin({
  key: new PluginKey('inlineSubtext'),
  appendTransaction: (
    transactions: Array<Transaction>,
    oldState: EditorState,
    newState: EditorState
  ) => {
    // Check if anything actually changed, and exit early if not.
    const stateChanged = isStateChanged(transactions, oldState, newState)
    if (!stateChanged) {
      return null
    }

    let {tr} = newState

    return reduceChangedDescendants(
      {
        old: oldState.doc,
        cur: newState.doc
      },
      (tr: Transaction, {node, pos}: NodeWithPos) => {
        if (node.isTextblock) {
          const nodeFrom = pos
          const nodeTo = getNodeToPos(node, pos)
          const nodeInsideFrom = getNodeInsideFromPos(pos)
          const nodeInsideTo = getNodeInsideToPos(node, pos)

          // Remove all previous marks in this node. We're going to re-render
          // the markup given the new content.
          tr = tr.removeMark(nodeFrom, nodeTo)

          const text = node.textContent
          return reduce(
            parseInlineSubtext(text),
            (tr, token): Transaction => {
              // Adjust range from local to global coords
              offsetRangeMut(token.range, nodeInsideFrom, nodeInsideTo)
              switch (token.type) {
                case TokenType.bold:
                  return renderBoldMark(tr, token.range)
                case TokenType.italic:
                  return renderItalicMark(tr, token.range)
                case TokenType.code:
                  return renderCodeMark(tr, token.range)
                case TokenType.wikilink:
                  return renderWikilinkMark(tr, token.range)
                case TokenType.bracketlink:
                  return renderBracketlinkMark(tr, token.range)
                case TokenType.barelink:
                  return renderBarelinkMark(
                    tr,
                    token.range,
                    text
                  )
                case TokenType.slashlink:
                  return renderSlashlinkMark(tr, token.range)
                default:
                  return tr
              }
            },
            tr
          )
        }
        return tr
      },
      tr
    )
  }
})

const renderBoldMark = (
  tr: Transaction,
  [from, to]: Range
) => tr.addMark(
  from,
  to,
  schema.marks.b.create()
)

const renderItalicMark = (
  tr: Transaction,
  [from, to]: Range
) => tr.addMark(
  from,
  to,
  schema.marks.i.create()
)

const renderCodeMark = (
  tr: Transaction,
  [from, to]: Range
) => tr.addMark(
  from,
  to,
  schema.marks.code.create()
)

const renderWikilinkMark = (
  tr: Transaction,
  [from, to]: Range
) => tr.addMark(
  from,
  to,
  schema.marks.wikilink.create()
)

const renderBracketlinkMark = (
  tr: Transaction,
  [from, to]: Range
) => tr.addMark(
  from,
  to,
  schema.marks.bracketlink.create()
)

const renderBarelinkMark = (
  tr: Transaction,
  [from, to]: Range,
  string: string
) => {
  return tr.addMark(
    from,
    to,
    schema.marks.barelink.create({
      href: string.slice(from, to)
    })
  )
}

const renderSlashlinkMark = (
  tr: Transaction,
  [from, to]: Range
) => tr.addMark(
  from,
  to,
  schema.marks.slashlink.create()
)