import {TranscludeModel} from '../common/transclude.js'
import {Slashlink} from '../common/slashlink.js'
import * as Dummy from './dummy-content.js'
import {sleep} from '../common/sleep.js'
import {indexById} from 'spellcaster/hyperscript.js'

type TranscludeService = {
  fetch: (slashlink: Slashlink) => Promise<TranscludeModel>,
  fetchBatch: (
    slashlinks: Array<Slashlink>
  ) => Promise<Map<string, TranscludeModel>>,
  destroy: () => void
}

export const DummyTranscludeService = (): TranscludeService => {
  const fetch = async (slashlink: Slashlink) => {
    await sleep(100)
    return Dummy.generateTransclude(slashlink)
  }

  const fetchBatch = async (
    slashlinks: Array<Slashlink>
  ): Promise<Map<string, TranscludeModel>> => {
    const transcludes = slashlinks.map(Dummy.generateTransclude)
    return indexById(transcludes)
  }

  const destroy = () => {}

  return {
    fetch,
    fetchBatch,
    destroy
  }
}