export type Range = [number, number]

export type ParserState = {
  string: string,
  from: number,
  to: number,
  saved: number
}

export const ParserState = ({
  string: string,
  from: anchor = 0,
  to: head = 0,
  saved = 0
}: Partial<ParserState>): ParserState => ({
  string,
  from: anchor,
  to: head,
  saved
})

export const isExhausted = (state: ParserState) =>
  state.to >= state.string.length

export const isAtBeginning = (state: ParserState) => state.to === 0

const max = Math.max
const min = Math.min

/** Bound a number between a min and max range, inclusive */
export const bounded = (
  n: number,
  from: number,
  to: number
) => min(max(n, from), to)

export const consume = (state: ParserState) => bounded(
  state.to++,
  0,
  state.string.length
)

export const peek = ({string, to}: ParserState, offset: number = 0) => {
  const i = bounded(
    to + offset,
    0,
    string.length
  )
  return string[i]
}

export const consumeMatch = (
  state: ParserState,
  literal: string
): boolean => {
  for (let i = 0; i < literal.length; i++) {
    if (state.string[state.to + i] !== literal[i]) {
      return false
    }
  }
  state.to += literal.length
  return true
}

/**
 * Consume until we find a literal match.
 * Note that this function does not backtrack on failure. It's up to you to
 * save state before calling it and backtrack if it fails.
 * Returns true if successful, false if not.
 */
export const consumeUntilMatch = (
  state: ParserState,
  literal: string
): boolean => {
  while (!isExhausted(state)) {
    if (consumeMatch(state, literal)) {
      return true
    }
    consume(state)
  }
  return false
}

export const consumeUntilMatchOrEnd = (
  state: ParserState,
  literal: string
): boolean => {
  while (!isExhausted(state)) {
    if (consumeMatch(state, literal)) {
      return true
    }
    consume(state)
  }
  return true
}

export const cut = (state: ParserState): Range => {
  const from = state.from
  const to = state.to
  state.from = state.to
  return [from, to]
}

export const start = (state: ParserState) => {
  state.from = state.to
  return state
}

export const save = (state: ParserState) => {
  state.saved = state.to
  return state
}

export const backtrack = (state: ParserState) => {
  state.to = state.saved
  return state
}

/**
 * Hand over parsing to a different arm of the recursive descent parser.
 * 
 * `consumeArm` is a function that takes a parser state and an AST,
 * and is expected to mutate both as needed.
 * 
 * If `consumeArm` returns `false`, the parser state is reset to the previous
 * position + 1, and the function returns `false`.
 */
export const delegate = <Ast>(
  consumeArm: (state: ParserState, ast: Ast) => boolean,
  state: ParserState,
  ast: Ast
): boolean => {
  // Save state
  save(state)
  const isSuccess = consumeArm(state, ast)
  if (!isSuccess) {
    // Backtrack to savepoint, since we failed.
    backtrack(state)
    // Advance by one, since we want to try again from the next position
    // during the next iteration.
    consume(state)
  }
  return isSuccess
}

/**
 * Offset the `from` position of a range, bounded by zero and `to`.
 * Mutates the range.
 * Returns the range.
 */
export const offsetFromMut = (range: Range, offset: number) => {
  const [from, to] = range
  range[0] = bounded(from + offset, 0, to)
  return range
}

/**
 * Offset the `to` position of a range, bounded by `from` and `length`.
 * Mutates the range.
 * Returns the range.
 */
export const offsetToMut = (range: Range, offset: number, length: number) => {
  const [from, to] = range
  range[1] = bounded(to + offset, from, length)
  return range
}

/**
 * Shift entire range by some offset, bounded by zero on and length.
 * Mutates the range.
 * Returns the range.
 */
export const offsetRangeMut = (
  range: Range,
  offset: number,
  length: number
) => {
  const [from, to] = range
  range[0] = bounded(offset + from, 0, length)
  range[1] = bounded(offset + to, 0, length)
  return range
}

export const sliceState = ({string, from, to}: ParserState): string =>
  string.slice(from, to)

/** Slice a string using a range */
export const slice = (string: string, [from, to]: Range) =>
  string.slice(from, to)

/**
 * Get the last char pointed to by a range.
 * Ranges are exclusive, so `to` is the index for the first character outside
 * of the range.
 */
export const lastCharInRange = (string: string, [from, to]: Range) => {
  if (from >= to) {
    return ''
  }
  const i = bounded(to - 1, from, to)
  return string[i]
}

export const firstCharInRange = (string: string, [from, to]: Range) => {
  if (from >= to) {
    return ''
  }
  return string[from]
}

export const isTrailingPunctuation = (char: string) => (
  char === '.' ||
  char === ',' ||
  char === '!' ||
  char === '?' ||
  char === ':' ||
  char === ';'
)